import { createSlice } from "@reduxjs/toolkit";
import { ProductState } from "../../components/models/product/product";

const initialState : ProductState = {
    id: "",
    description: "",
    stock: 0,
    category: "",
    name: "",
    price: 0,
    imageUrl: [],
    quantity: 0,
};

const productSlice = createSlice({
    name: "product",
    initialState,
    reducers: {
        setProduct: (state, action) => {
            const { name, price, imageUrl, quantity, id, description, stock } = action.payload;
            state.name = name;
            state.price = price;
            state.imageUrl = imageUrl;
            state.quantity = quantity;
            state.id = id;
            state.description = description;
            state.stock = stock;

        },
        setQuantityCheckout: (state, action) => {
            state.quantity = action.payload;
        }
    },
});

export const { setProduct, setQuantityCheckout } = productSlice.actions;
export default productSlice.reducer;