import { ProductModel } from "../../components/models/product/product";
import axiosInstance from "../axiosIntance";

export const ProductServices = {
    getProducts: async () => {
        try {
        const res = await axiosInstance.get('/products');
        return res.data;
        } catch (err: any) {
        console.error('Failed to fetch products:', err.response?.data?.error || err.message);
        }
    },
    
    getProductById: async (id: number): Promise<ProductModel | undefined> => {
        try {
        const res = await axiosInstance.get(`/products/${id}`);
        return res.data;
        } catch (err: any) {
        console.error('Failed to fetch product:', err.response?.data?.error || err.message);
        }
    },
    
    createProduct: async (productData: any) => {
        try {
        const res = await axiosInstance.post('/products', productData);
        return res.data;
        } catch (err: any) {
        console.error('Failed to create product:', err.response?.data?.error || err.message);
        }
    },
    
    updateProduct: async (id: string, productData: any) => {
        try {
        const res = await axiosInstance.put(`/products/${id}`, productData);
        return res.data;
        } catch (err: any) {
        console.error('Failed to update product:', err.response?.data?.error || err.message);
        }
    },
    
    deleteProduct: async (id: string) => {
        try {
        const res = await axiosInstance.delete(`/products/${id}`);
        return res.data;
        } catch (err: any) {
        console.error('Failed to delete product:', err.response?.data?.error || err.message);
        }
    },
}