import { useNavigate, useLocation } from 'react-router-dom';
import { styled } from '@mui/material/styles';
import { Card, CardContent, Typography, Box, Button } from '@mui/material';

// Styled components
const PageWrapper = styled('div')(({ theme }) => ({
  minHeight: '100vh',
  backgroundColor: theme.palette.primary.main + '33', // bg-primary/20
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  padding: '3rem 1.5rem',
}));

const StyledCard = styled(Card)(({ theme }) => ({
  maxWidth: '28rem', // max-w-md
  width: '100%',
  backgroundColor: theme.palette.primary.main + '4D', // bg-primary/30
  backdropFilter: 'blur(6px)', // backdrop-blur-sm
  border: 'none',
  padding: '2rem',
}));

const IconWrapper = styled(Box)(({ theme }) => ({
  width: '4rem',
  height: '4rem',
  borderRadius: '50%',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  margin: '0 auto 1.5rem',
}));

const OrderDetailsBox = styled(Box)(({ theme }) => ({
  backgroundColor: theme.palette.primary.main + '80', // bg-primary/50
  borderRadius: theme.shape.borderRadius,
  padding: '1rem',
  marginBottom: '1.5rem',
  textAlign: 'left',
}));

const OrderSuccess = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { orderResponse } = location.state || {};

  if (!orderResponse) {
    return (
      <PageWrapper>
        <StyledCard>
          <CardContent>
            <Typography variant="h6" color="secondary">
              No order data found.
            </Typography>
            <Button variant="contained" onClick={() => navigate('/')}>
              Go Home
            </Button>
          </CardContent>
        </StyledCard>
      </PageWrapper>
    );
  }

  const { message, orderId, totalQuantity, grandTotal } = orderResponse;

  return (
    <PageWrapper>
      <StyledCard>
        <CardContent>
          <Box textAlign="center">
            <IconWrapper>
            <img 
            src="/img/icon.png" // replace with your actual path
            alt="Order Icon" 
            style={{ width: '100%', height: '100%' }} 
          />
                    </IconWrapper>

            <Typography variant="h4" component="h1" color="secondary" fontWeight={600} gutterBottom>
              {message || 'Order Confirmed!'}
            </Typography>

            <Typography variant="body2" color="secondary" sx={{ opacity: 0.7, mb: 3 }}>
              Thank you for your order. We've received your order and will process it shortly.
            </Typography>

            <OrderDetailsBox>
              <Typography variant="h6" color="secondary" fontWeight={500} mb={1}>
                Order Details
              </Typography>
              <Typography variant="body2" color="secondary" sx={{ opacity: 0.7 }}>
                Order #: {orderId}
              </Typography>
              <Typography variant="body2" color="secondary" sx={{ opacity: 0.7 }}>
                Total Quantity: {totalQuantity}
              </Typography>
              <Typography variant="body2" color="secondary" sx={{ opacity: 0.7 }}>
                Grand Total: ${grandTotal.toFixed(2)}
              </Typography>
            </OrderDetailsBox>

            <Button 
              variant="contained" 
              fullWidth 
              onClick={() => navigate('/')}
              sx={{
                backgroundColor: theme => theme.palette.secondary.main,
                color: theme => theme.palette.primary.main,
                '&:hover': {
                  backgroundColor: theme => theme.palette.secondary.main + 'E6',
                },
              }}
            >
              Continue Shopping
            </Button>
          </Box>
        </CardContent>
      </StyledCard>
    </PageWrapper>
  );
};

export default OrderSuccess;
