import React, { useEffect, useRef, useState } from 'react';
import { CardContainer, CarouselWrapper, CustomContainer, HeroWrapper, ImageCard, NavButton } from './hero.styled';

const images = [
  '/img/klarity-karen.jpg',
  '/img/klarity-cover.jpg',
  '/img/klarity-karen-product.jpg'
];

const MAX_VISIBILITY = 3;

const Carousel = ({ images }: { images: string[] }) => {
  const [active, setActive] = useState(1);
  const count = images.length;
  const timerRef = useRef<NodeJS.Timeout | null>(null);

  const startAutoPlay = () => {
    if (timerRef.current) clearInterval(timerRef.current);
    timerRef.current = setInterval(() => {
      setActive((prev) => (prev + 1) % count);
    }, 3000);
  };

  useEffect(() => {
    startAutoPlay();
    return () => {
      if (timerRef.current) clearInterval(timerRef.current);
    };
  }, [count]);

  const goPrev = () => {
    setActive((prev) => (prev - 1 + count) % count);
    startAutoPlay();
  };

  const goNext = () => {
    setActive((prev) => (prev + 1) % count); 
    startAutoPlay();
  };

  return (
    <CarouselWrapper>
      {count > 1 && (
        <NavButton direction="left" onClick={goPrev}>
          {`<`}
        </NavButton>
      )}

{/* {images.map((src, i) => {
  const offset = (active - i) / 3;
  const absOffset = Math.abs(active - i) / 3;
  const direction = Math.sign(active - i);
  const isActive = i === active;

  const zIndex = 1000 - Math.abs(active - i); // Higher zIndex for closer cards

  const style: React.CSSProperties = {
    transform: `
      rotateY(${offset * 50}deg)
      scaleY(${1 + absOffset * -0.4})
      translateZ(${absOffset * -480}px)
      translateX(${direction * -80}px)
    `,
    filter: `blur(${absOffset * 16}px)`,
    opacity: Math.abs(active - i) >= MAX_VISIBILITY ? 0 : 1,
    pointerEvents: isActive ? 'auto' : 'none',
    display: Math.abs(active - i) > MAX_VISIBILITY ? 'none' : 'block',
  };

        return (
          <CardContainer key={i} $style={style}  $zIndex={zIndex}>
            <ImageCard>
              <img
                src={src}
                alt={`Slide ${i}`}
                onError={(e) => {
                  e.currentTarget.src = 'https://placehold.co/600x800?text=Beautiful+Skin';
                }}
              />
            </ImageCard>
          </CardContainer>
        );
      })} */}

{images.map((src, i) => {
  // Calculate the shortest distance in a circular list
  let distance = (i - active + count) % count;
  if (distance > count / 2) {
    distance -= count;
  }

  const offset = -distance / 3;
  const absOffset = Math.abs(distance) / 3;
  const direction = Math.sign(-distance);
  const isActive = distance === 0;

  const zIndex = 1000 - Math.abs(distance);

  const style: React.CSSProperties = {
    transform: `
      rotateY(${offset * 50}deg)
      scaleY(${1 + absOffset * -0.4})
      translateZ(${absOffset * -480}px)
      translateX(${direction * -80}px)
    `,
    filter: `blur(${absOffset * 16}px)`,
    opacity: Math.abs(distance) >= MAX_VISIBILITY ? 0 : 1,
    pointerEvents: isActive ? 'auto' : 'none',
    display: Math.abs(distance) > MAX_VISIBILITY ? 'none' : 'block',
    transition: 'transform 0.5s ease, filter 0.5s ease, opacity 0.5s ease'
  };

  return (
    <CardContainer key={i} $style={style} $zIndex={zIndex}>
      <ImageCard>
        <img
          src={src}
          alt={`Slide ${i}`}
          onError={(e) => {
            e.currentTarget.src = 'https://placehold.co/600x800?text=Beautiful+Skin';
          }}
        />
      </ImageCard>
    </CardContainer>
  );
})}


      {count > 1 && (
        <NavButton direction="right" onClick={goNext}>
          {`>`}
        </NavButton>
      )}

    </CarouselWrapper>
  );
};

const HeroSection = () => {
  return (
    <HeroWrapper>
      <CustomContainer>
        <Carousel images={images} />
      </CustomContainer>
    </HeroWrapper>
  );
};

export default HeroSection;

