import { useEffect, useState } from 'react';
import { 
  Box,Dialog, DialogContent, 
  Button, TextField, IconButton, 
  Alert
} from '@mui/material';
import { Close as CloseIcon } from '@mui/icons-material';
import { Formik } from 'formik';
import * as Yup from 'yup';
import { Logo, StyledAppBar } from '../../styledComponent';
import LoginIcon from '@mui/icons-material/Login';
import LogoutIcon from '@mui/icons-material/Logout';
import { AuthServices } from '../../../api/auth';
import { useDispatch, useSelector } from 'react-redux';
import { setUserEmail } from '../../../redux/auth';
import { HeaderProps } from './header.types';
import { StyledDialogTitle, StyledForm, StyledToolbar } from './header.styles';
import { Link } from 'react-router-dom';
import { RootState } from '../../../redux';

const loginSchema = Yup.object().shape({
  email: Yup.string().email('Invalid email').required('This field is required'),
});

const Header = ({open, setOpen} : HeaderProps) => {
  const [authError, setAuthError] = useState('');
  const userEmail = useSelector((state: RootState) => state.user.email);
  const dispatch = useDispatch();

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleEmailEnter =async (values: { email: string }) => {
    try {
      const user= await AuthServices.handleEmailSubmit(values);
      dispatch(setUserEmail(user.email));      
      handleClose();
    } catch (err: any) {
      setAuthError(err|| 'Please try again.');
      setTimeout(() => {
        setAuthError('');
      }, 5000);
    }
  };

  const logout = () => {
    dispatch(setUserEmail(''));
  }

  useEffect(() => {
    if (authError) {
      const timer = setTimeout(() => setAuthError(''), 5000);
      return () => clearTimeout(timer); 
    }
  }, [authError]);

  return (
    <StyledAppBar position="static">
      <StyledToolbar>
        <Link to="/" style={{ textDecoration: 'none', color: 'inherit' }}>
        <Logo>
             <img
            src="/img/logo-bold.png"
            alt="Klarity"
            style={{height: '35px', width: '160px' }}
          />
        </Logo>
        </Link>

        <Box display="flex" alignItems="center">
        {/* <Logo>
             <img
            src="/img/klarity icon.png"
            alt="Klarity"
            style={{height: '35px', width: '45px', marginRight: '-2px' }}
          />
        </Logo> */}
          {userEmail === "" ? (
            <Button
              variant="outlined"
              color="secondary"
              onClick={handleOpen}
              startIcon={<LoginIcon />}
              sx={{ borderColor: '#4a7061', color: '#4a7061' }}
            >
              Sign In
            </Button>
          ) : (
            <Button
              variant="outlined"
              color="secondary"
              onClick={logout}
              startIcon={<LogoutIcon />}

              sx={{ borderColor: '#4a7061', color: '#4a7061' }}
            >
              Logout
            </Button>
          )}
        </Box>
      </StyledToolbar>

      <Dialog open={open} onClose={handleClose} fullWidth maxWidth="xs" >
        <Box sx={{  position: 'relative', display: 'flex',alignItems: 'center', padding: '10px 0', backgroundColor: '#f0e8df' , height: '64px' }}>
          <StyledDialogTitle sx={{
            position: 'absolute',
            left: '43%',
            transform: 'translateX(-50%)',
            // marginLeft: "0.5rem",
            fontSize: {
              xs: '1rem',  
              sm: '1.25rem',  
            },
          }}>
            Login and get 10% off!
          </StyledDialogTitle >
          <IconButton onClick={handleClose}  sx={{
            position: 'absolute',
            right: '16px',
            color: '#4a7061'
          }}>
            <CloseIcon />
          </IconButton>
        </Box>

        <DialogContent >
        {authError && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {authError}
        </Alert>
)}
            <Formik
              initialValues={{ email: ''}}
              validationSchema={loginSchema}
              onSubmit={handleEmailEnter}
            >
              {({ errors, touched, handleChange, handleBlur, isValid, isSubmitting }) => (
                <StyledForm>
                  <TextField
                    fullWidth
                    name="email"
                    label="Email"
                    variant="outlined"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    error={touched.email && Boolean(errors.email)}
                    helperText={touched.email && errors.email}
                  />
                  <Button
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="secondary"
                    sx={{ backgroundColor: '#4a7061', '&:hover': { backgroundColor: '#3a5a4e' } }}
                    disabled={!isValid || isSubmitting}
                  >
                    Login
                  </Button>
                </StyledForm>
              )}
            </Formik>
        </DialogContent>
      </Dialog>
    </StyledAppBar>

  );
};

export default Header;